`, "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/main/applicationList.vue"],"names":[],"mappings":";AAgdI;QACI,YAAY;QACZ,gBAAgB;QAChB,cAAc;QACd,eAAe;QACf,gBAAgB;AACpB;AACA;QACI,6BAA6B;QAC7B,kBAAkB;QAClB,yBAAyB;QACzB,cAAc;QACd,kBAAkB;AACtB;AACA;QACI,eAAe;QACf,cAAc;AAClB;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;AAC9C;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,iBAAiB;AACrB;AACA;QACI,yBAAyB;QACzB;AACJ","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, defineAsyncComponent, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport ApplicationCard from \"../../components/application/appCard.vue\"; // resolvable by vue transpiler (bundled)\nimport AppDataTable from \"../../components/application/appDataTable.vue\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport \"bootstrap/js/dist/collapse\";\nimport { IOrganizationUseCase } from \"../../../../domain/useCase/IOrganizationUseCase\";\nimport { IAuthenticationUseCase } from \"../../../../domain/useCase/IAuthenticationUseCase\";\nimport CustomSelect from \"../../components/customSelect.vue\"\n\n\n// const AddOrUpdateApp = defineAsyncComponent(() => import(\"../../components/application/addOrUpdateApp.vue\"));\n// const ApplicationCard = defineAsyncComponent(() => import (\"../../components/application/applicationCard.vue\")); // resolvable by vue transpiler (lazy-load)\n// const ApplicationCard = (await import (\"\" + \"../../components/application/applicationCard.vue\")).default; // unresolvable by vue transpiler\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst router = container.resolve<IRouter>(IRouter);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst organization = container.resolve<IOrganizationUseCase>(IOrganizationUseCase);\nconst authentication = container.resolve<IAuthenticationUseCase>(IAuthenticationUseCase);\nconst routerParams = router.getRouteParams<{ orgId?: string }>();\n\nlet viewData = ref(\"Card\");\nconst organizationName = ref(\"\");\nconst selectedOrganizationId = ref(viewModel.user!.selectedOrganization?.id);\nconst selectedOrganizationName = computed(() => viewModel.user!.selectedOrganization?.name);\nconst selectedOrganizationCreaterId = computed(() => viewModel.user!.selectedOrganization?.creatorUserId);\nconst organizations = ref(['']);\nviewModel.user?.organizations.forEach(org => {\n    organizations.value.push(org.name);\n})\n\nfunction onOrganizationSelect(id?: string) {\n    selectedOrganizationId.value = id;\n    const newOrgId = selectedOrganizationId.value;\n    if (!newOrgId) { return; }\n    executor.execute<void>(async () => {\n        await organization.changeOrganization(newOrgId);\n        router.push(`/main/applicationlist/${newOrgId}`);\n    }, { loading: true });\n}\nfunction openCreateOrganizationDialog() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/organization/createOrganizationDialog.vue`)),\n        { closable: true, title: \"Create Organization\", width: \"520px\", height: \"206px\" },\n    );\n}\nfunction addEventListeners() {\n    document.getElementById(\"create-organization\")!.addEventListener(\"click\", openCreateOrganizationDialog);\n}\nfunction openInvitePeopleDialog() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/organization/invitePeopleDialog.vue`)),\n        { closable: true, title: \"Invite People to \" + selectedOrganizationName.value, width: \"658px\", maxHeight: \"700px\" },\n    );\n}\nfunction respondToInvitation(accept: boolean, invitationId: string, organizationId?: string) {\n    executor.execute(async () => {\n        await organization.respondToInvitation({ accept, invitationId });\n        // await organization.listOrganizations();\n        await authentication.signIn({});\n        if(accept) {\n            if (selectedOrganizationId.value && selectedOrganizationId.value?.length > 0) {\n                await onOrganizationSelect(selectedOrganizationId.value);\n            }\n            else {\n                await onOrganizationSelect(organizationId!);\n            }\n        }\n    }, { loading: true });\n}\nfunction onManageOrganization() {\n    router.push(`/organization/manage/${selectedOrganizationId.value}`);\n}\n\nfunction CreateApp(templateName?: string) {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Create Application\", height: \"383px\", width: \"466px\" },\n        { templateName }\n    );\n}\n\nfunction changeView(view: any) {\n    viewData.value = view;\n    let tableVButton = document.getElementById(\"TableView\");\n    let cardVButton = document.getElementById(\"CardView\");\n    if (viewData.value == \"Table\") {\n        cardVButton?.classList.remove(\"activeView\");\n        tableVButton?.classList.add(\"activeView\");\n    } else if (viewData.value == \"Card\") {\n        tableVButton?.classList.remove(\"activeView\");\n        cardVButton?.classList.add(\"activeView\");\n    }\n}\nexecutor.execute(async ()  => {\n    if(!(selectedOrganizationId.value && selectedOrganizationId.value?.length > 0) && viewModel.user!.organizations.length > 0) {\n        onOrganizationSelect(viewModel.user?.organizations[0].id);\n    }\n        }, {loading: false}) \n</script>\n\n<template>\n    <div v-if=\"viewModel.user?.organizations.length == 0\" class=\"organization-div\">\n        <span class=\"col-xl-4\" style=\"color:#212121; align-items: center; display: flex;\">You have not joined an organization yet.</span>\n        <div style=\"justify-content: end; display: flex; width: 100%\">\n            <button class=\"create-organization-button\" @click=\"openCreateOrganizationDialog\"><i class=\"mdi mdi-plus\" style=\"font-size: 20px; margin-right: 4px;\"></i>CREATE ORGANIZATION</button>\n        </div>\n    </div>\n    <div v-else class=\"organization-div\">\n        <div>\n              <CustomSelect @input=\"onOrganizationSelect\" @click = \"addEventListeners()\" :options=\"viewModel.user?.organizations\" :default = \"viewModel.user?.selectedOrganization?.name ? viewModel.user?.selectedOrganization?.name : viewModel.user?.organizations[0].name\"\n              :selectedClass = \"'selected-org'\" :extra-button=\"true\"  :buttons=\"[{buttonText: '+ Create Organization', buttonClass: 'create-organization-in-options', buttonID: 'create-organization'} ]\" \n              class=\"select-organization\"></CustomSelect>\n        </div>\n        <div v-if=\"selectedOrganizationCreaterId == viewModel.user?.userId\" style=\"display: flex; justify-content: end; width: 100%\">\n              <button class=\"invite-button\" @click=\"openInvitePeopleDialog\"><i class=\"mdi mdi-account-multiple-plus\" style=\"font-size:20px; margin-right: 4px;\"></i>INVITE PEOPLE</button>\n              <button class=\"manage-organization-button\" @click=\"onManageOrganization\"><i class=\"mdi mdi-cog\" style=\"font-size:20px; margin-right: 4px;\"></i>MANAGE ORGANIZATION</button>\n        </div>\n    </div>\n    <div v-for=\"invitation in viewModel.user?.invitations\" class=\"invitation-row\">\n        <div class=\"invitation-data\">\n            <img src=\"../../../../images/profile-image-dark-in-menu.svg\" style=\"margin-right: 16px;\"/>\n            <span><b>{{ invitation.inviter.name + ' ' + invitation.inviter.lastName }}</b> invited you to join <b>{{ invitation.organizationName }}</b> organization.</span>\n        </div>\n        <div class=\"invitation-data\">\n            <button class=\"invitation-respond-button\" @click=\"respondToInvitation(true, invitation.id, invitation.organizationId)\"><i class=\"mdi mdi-check\" style=\"color:#29C660; font-size: 16px; margin-right: 4px;\"></i>Accept</button>\n            <button class=\"invitation-respond-button\" @click=\"respondToInvitation(false, invitation.id)\"><i class=\"mdi mdi-close\" style=\"color:#FF6B6B; font-size: 16px; margin-right: 4px;\"></i>Reject</button>\n        </div>\n    </div>\n    <p class=\"app-text\">Create New Application</p>\n    <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"createapp-container\">\n        <div class=\"text\"></div>\n        <div class=\"blankapp-logo-card\" @click=\"CreateApp()\">\n            <img src=\"../../../../images/blank-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n        </div>\n        <p class=\"title\">Blank Application</p>\n        <div class=\"blankapp-logo-card\" style=\"left: 211px\" @click=\"CreateApp('newAppTemplate1')\">\n            <img src=\"../../../../images/template-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n        </div>\n        <p class=\"title\" style=\"left: 226px\">Template Application</p>\n    </div>\n    <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"filter-text-container\">\n        <span class=\"applications-text\">{{selectedOrganizationName}} Applications</span>\n        <div class=\"card-filters\">\n            <i class=\"mdi mdi-view-dashboard dashboard-icon activeView\" id=\"CardView\" @click=\"changeView('Card')\"></i>\n            <i class=\"mdi mdi-format-list-bulleted bulleted-icon\" id=\"TableView\" @click=\"changeView('Table')\"></i>\n        </div>\n    </div>\n    <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"cards\">\n        <div v-if=\"viewModel.apps.length == 0\" class=\"empty-org\">\n            <div style=\"justify-content: center; display: grid; margin-bottom: 36px;\">\n                <img style=\" margin: 36px auto 12px;\" src=\"../../../../images/ghost.svg\"/>\n                <span style=\"font-size: 18px; color: #BCBCBC\">It's been empty for a while.</span>\n            </div>\n        </div>\n        <div v-else class=\"row relative-for-children\">\n            <div v-if=\"viewData == 'Card'\" class=\"card-media\" style=\"padding: 0px\" v-for=\"app in viewModel.apps.sort((a, b) =>\n                a.createDate && b.createDate ? (new Date(a.createDate).getTime() - new Date(b.createDate).getTime() > 0 ? -1 : 1) : 1\n            )\" :key=\"app.ID\" v-bind:value=\"app.ID\">\n                <ApplicationCard :app=\"app\" />\n            </div>\n            <div v-if=\"viewData == 'Table'\">\n                <AppDataTable />\n            </div>\n        </div>\n    </div>\n    <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length == 0\">\n        <div class=\"need-organization\">\n            <span>To create an application, you must be in an organization.</span>\n        </div>\n        <span class=\"applications-text\">Your Applications</span>\n        <div class=\"create-org-div\">\n            <div style=\"justify-content: center; display: grid; margin-bottom: 36px;\">\n                <img style=\" margin: 36px auto 12px;\" src=\"../../../../images/ghost.svg\"/>\n                <span style=\"font-size: 22px; color: #BCBCBC\">It's been empty for a while.</span>\n            </div>\n            <!-- <button class=\"create-organization-button-appList\" @click=\"openCreateOrganizationDialog\"><i class=\"mdi mdi-plus\" style=\"font-size: 20px; margin-right: 4px;\"></i>CREATE ORGANIZATION</button> -->\n        </div>\n    </div>\n</template>\n\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n* {\n    font-family: \"Roboto\";\n}\n\n\n.invitation-row {\n    display: flex;\n    justify-content: space-between;\n    margin: 0 -33px;\n    background-color: #f5f5f5;\n    padding: 10px 24px;\n    border-bottom: 1px solid #D5D5D5;\n    align-items: center;\n}\n\n.invitation-data {\n    /* flex-basis: 25%; */\n    /* text-align: center; */\n}\n\n.invitation-label {\n    font-weight: bold;\n    margin-bottom: 5px;\n}\n\n.invitation-value {\n    color: blue;\n}\n\n.relative-for-children {\n    position: relative;\n    margin-left: 0px;\n}\n\n.blankapp-logo {\n    position: absolute;\n    top: calc((100% - 57px) / 2);\n    left: calc((100% - 57px) / 2);\n}\n\n.blankapp-logo-card {\n    width: 163px;\n    height: 119px;\n    border-radius: 4px !important;\n    position: absolute;\n    background: #eeeeee 0% 0% no-repeat padding-box;\n    border: 1px solid #d5d5d5;\n    top: 20px;\n    left: 24px;\n    cursor: pointer;\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n    overflow: hidden;\n    background-color: #EEEEEE !important;\n}\n\n.applications-text {\n    font-size: 22px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n}\n\n.app-text {\n    font-size: 18px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n    margin-top: 32px;\n}\n\n.createapp-container {\n    position: relative;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n    width: 100%;\n    height: 187px;\n    min-width: 425px;\n    margin-top: 12px;\n}\n\n.title {\n    position: absolute;\n    top: 147px;\n    left: 48px;\n    color: #151233;\n    font-size: 15px;\n}\n\n.filter-text-container {\n    display: flex;\n    justify-content: space-between;\n    margin-top: 35px;\n}\n\n.text {\n    position: absolute;\n    left: 24px;\n    top: 24px;\n}\n\n.dashboard-icon,\n.bulleted-icon {\n    font-size: 30px;\n    margin: 10px;\n    opacity: 0.5;\n    color: #151233;\n}\n\n.card-filters {\n    cursor: pointer;\n    margin-top: -12px;\n}\n\n.cards {\n    margin-top: 10px;\n}\n\n.activeView {\n    opacity: 1;\n}\n.organization-div {\n    border-bottom: 1px solid #E3E3E3;\n    margin: -22px -33px 0;\n    padding: 14px 24px;\n    display: flex;\n    align-items: center;\n    background: #EEEEEE;\n}\n.create-organization-button {\n    background: #2C5A86; \n    border-radius: 4px; \n    border: none; \n    padding: 8px 16px;\n    font-size: 15px; \n    font-weight: 500; \n    color: #FFFFFF;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height: 36px;\n    justify-content: center;\n}\n.create-organization-button-appList {\n    background: #2C5A86; \n    border-radius: 4px; \n    border: none; \n    padding: 8px 20px;\n    font-size: 17px; \n    font-weight: 500; \n    color: #FFFFFF;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    height: 48px;\n    justify-content: center;\n}\n.need-organization {\n    background: #FFFFFF;\n    color:#BCBCBC;\n    align-items: flex-start;\n    padding: 20px 24px;\n    border-radius: 10px;\n    font-size: 22px;\n    margin-bottom: 32px;\n}\n.create-org-div, \n.empty-org {\n    display: grid;\n    align-items: center;\n    justify-content: center;\n}\n.invitation-respond-button {\n    background: transparent;\n    border: none;\n    padding: 7px 16px;\n    border-radius: 4px;\n    font-weight: 500;\n    font-size: 14px;\n    color: #151233;\n}\n.invite-button {\n  background: #2C5A86; \n  border-radius: 4px; \n  border: none; \n  padding: 8px 16px;\n  font-size: 15px; \n  font-weight: 500; \n  color: #FFFFFF;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  height: 36px;\n  margin-right: 12px;\n}\n.manage-organization-button {\n  border: 1px solid #151233;\n  border-radius: 4px; \n  padding: 8px 16px;\n  font-size: 15px; \n  font-weight: 500; \n  color: #151233;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  height: 36px;\n}\n\n.select-organization {\n  width: 260px;\n  font-weight: 500;\n  font-size: 15px;\n  height: 36px;\n  line-height: 36px;\n}\n\n@media (min-width: 2400px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 8);\n    }\n}\n\n@media (max-width: 2400px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 7);\n    }\n}\n\n@media (max-width: 2150px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 6);\n    }\n}\n\n@media (max-width: 1820px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: calc(100% / 5);\n    }\n}\n\n@media (max-width: 1550px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 4);\n    }\n}\n\n@media (max-width: 1250px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 3);\n    }\n}\n\n@media (max-width: 950px) {\n    .card-media {\n        flex: 1 0 auto;\n        width: calc(100% / 2);\n    }\n}\n\n@media (max-width: 700px) {\n    .card-media {\n        flex: 0 0 auto;\n        width: 100%;\n    }\n}</style>\n<style>\n    .create-organization-in-options {\n        border: none;\n        background: none;\n        color: #616161;\n        font-size: 15px;\n        font-weight: 500;\n    }\n    .selected-org {\n        background-color: transparent;\n        border-radius: 4px;\n        border: 1px solid #BCBCBC;\n        color: #212121;\n        padding-left: 12px;\n    }\n    .selected-org i {\n        font-size: 20px;\n        color: #BCBCBC;\n    }\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    }\n    .custom-select .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #2C5A86;\n        color:#FFFFFF\n    }\n</style>\n"],"sourceRoot":""}]);
